// TODO: Replace with cache adapter

import { type IAgentRuntime, type Memory, type Route, createUniqueUuid } from '@elizaos/core';

import { SentimentArraySchema, TweetArraySchema } from './schemas';

import fs from 'node:fs';
import path from 'node:path';
import { fileURLToPath } from 'node:url';
import type { IToken, ITransactionHistory, Sentiment } from './types';

// Define the equivalent of __dirname for ES modules
const __filename = fileURLToPath(import.meta.url);
const __dirname = path.dirname(__filename);

/**
 * Definition of routes with type, path, and handler for each route.
 * Routes include fetching trending tokens, wallet information, tweets, sentiment analysis, and signals.
 */

export const routes: Route[] = [
  {
    type: 'GET',
    path: '/degenintel-test',
    public: true,
    name: 'Degen Intel Test',
    handler: async (_req: any, res: any) => {
      res.json({ message: 'Degen Intel routes are working!', timestamp: new Date().toISOString() });
    },
  },
  {
    type: 'GET',
    path: '/degen-intel/dashboard',
    public: true,
    name: 'Degen Intel Dashboard',
    handler: async (_req: any, res: any) => {
      console.log('=== DEGEN INTEL DASHBOARD ROUTE HIT ===');
      console.log('Request path:', _req.path);
      console.log('Request URL:', _req.url);
      
      // Serve the built index.html from the dist directory
      const indexPath = path.resolve(process.cwd(), 'dist/index.html');
      console.log('Serving index.html from:', indexPath);
      console.log('File exists:', fs.existsSync(indexPath));
      
      if (fs.existsSync(indexPath)) {
        res.sendFile(indexPath);
      } else {
        res.status(404).send('Frontend not built. Please run: yarn build');
      }
    },
  },
  {
    type: 'GET',
    path: '/degen-intel/dashboard/*',
    public: true,
    handler: async (_req: any, res: any) => {
      // Serve the built index.html for all sub-routes (SPA routing)
      const indexPath = path.resolve(process.cwd(), 'dist/index.html');
      if (fs.existsSync(indexPath)) {
        res.sendFile(indexPath);
      } else {
        res.status(404).send('Frontend not built. Please run: yarn build');
      }
    },
  },
  {
    type: 'GET',
    path: '/degen-intel/assets/:filename',
    public: true,
    handler: async (req: any, res: any) => {
      // Serve static assets from the dist/assets directory
      const filename = req.params.filename;
      const filePath = path.resolve(process.cwd(), 'dist', 'assets', filename);
      
      console.log('Asset request:', {
        filename,
        filePath,
        exists: fs.existsSync(filePath)
      });
      
      if (fs.existsSync(filePath)) {
        // Set appropriate content type
        if (filename.endsWith('.js')) {
          res.setHeader('Content-Type', 'application/javascript');
        } else if (filename.endsWith('.css')) {
          res.setHeader('Content-Type', 'text/css');
        }
        res.sendFile(filePath);
      } else {
        res.status(404).send('Asset not found');
      }
    },
  },
  {
    type: 'POST',
    path: '/trending',
    handler: async (_req: any, res: any, runtime) => {
      try {
        const cachedTokens = await runtime.getCache<IToken[]>('tokens_solana');
        const tokens: IToken[] = cachedTokens ? cachedTokens : [];
        const sortedTokens = tokens.sort((a, b) => (a.rank || 0) - (b.rank || 0));
        res.json(sortedTokens);
      } catch (_error) {
        res.status(500).json({ error: 'Internal server error' });
      }
    },
  },
  {
    type: 'POST',
    path: '/wallet',
    handler: async (_req: any, res: any, runtime: IAgentRuntime) => {
      try {
        // Get transaction history
        const cachedTxs = await runtime.getCache<ITransactionHistory[]>('transaction_history');
        const transactions: ITransactionHistory[] = cachedTxs ? cachedTxs : [];
        const history = transactions
          .filter((tx: any) => tx.data.mainAction === 'received')
          .sort((a, b) => new Date(b.blockTime).getTime() - new Date(a.blockTime).getTime())
          .slice(0, 100);

        // Get portfolio
        const cachedPortfolio = await runtime.getCache<any>('portfolio');
        const portfolio = cachedPortfolio ? cachedPortfolio : { key: 'PORTFOLIO', data: null };

        res.json({ history, portfolio: portfolio.data });
      } catch (_error) {
        res.status(500).json({ error: 'Internal server error' });
      }
    },
  },
  {
    type: 'GET',
    path: '/tweets',
    handler: async (_req: any, res: any, runtime: IAgentRuntime) => {
      try {
        const memories = await runtime.getMemories({
          tableName: 'messages',
          roomId: createUniqueUuid(runtime, 'twitter-feed'),
          end: Date.now(),
          count: 50,
        });

        const tweets = memories
          .filter((m) => m.content.source === 'twitter')
          .sort((a, b) => b.createdAt - a.createdAt)
          .map((m) => ({
            text: m.content.text,
            timestamp: m.createdAt,
            metadata: m.content.tweet || {},
          }));

        const validatedData = TweetArraySchema.parse(tweets);
        res.json(validatedData);
      } catch (_error) {
        res.status(500).json({ error: 'Internal server error' });
      }
    },
  },
  {
    type: 'GET',
    path: '/sentiment',
    handler: async (_req: any, res: any, runtime: IAgentRuntime) => {
      try {
        const memories = await runtime.getMemories({
          tableName: 'messages',
          roomId: createUniqueUuid(runtime, 'sentiment-analysis'),
          end: Date.now(),
          count: 30,
        });

        const sentiments = memories
          .filter(
            (m): m is Memory & { content: Sentiment } =>
              m.content.source === 'sentiment-analysis' &&
              !!m.content.metadata &&
              typeof m.content.metadata === 'object' &&
              m.content.metadata !== null &&
              'processed' in m.content.metadata &&
              'occuringTokens' in m.content.metadata &&
              Array.isArray((m.content.metadata as any).occuringTokens) &&
              (m.content.metadata as any).occuringTokens.length > 1
          )
          .sort((a, b) => {
            const aMetadata = a.content.metadata as any;
            const bMetadata = b.content.metadata as any;
            const aTime = new Date(aMetadata.timeslot).getTime();
            const bTime = new Date(bMetadata.timeslot).getTime();
            return bTime - aTime;
          })
          .map((m) => {
            const metadata = m.content.metadata as any;
            return {
              timeslot: metadata.timeslot,
              text: m.content.text,
              processed: metadata.processed,
              occuringTokens: metadata.occuringTokens || [],
            };
          });

        const validatedData = SentimentArraySchema.parse(sentiments);
        res.json(validatedData);
      } catch (_error) {
        res.status(500).json({ error: 'Internal server error' });
      }
    },
  },
  {
    type: 'POST',
    path: '/signal',
    handler: async (_req: any, res: any, runtime: IAgentRuntime) => {
      try {
        const cachedSignal = await runtime.getCache<any>('BUY_SIGNAL');
        const signal = cachedSignal ? cachedSignal : {};
        res.json(signal?.data || {});
      } catch (_error) {
        res.status(500).json({ error: 'Internal server error' });
      }
    },
  },
];

export default routes;
